import { Connection, PublicKey, SystemProgram, Transaction } from "@solana/web3.js";
import { ASSOCIATED_TOKEN_PROGRAM_ID, createAssociatedTokenAccountInstruction, createTransferInstruction, getAccount, getAssociatedTokenAddress, TOKEN_PROGRAM_ID, TokenAccountNotFoundError, TokenInvalidAccountOwnerError, } from "@solana/spl-token";
import BN from "bn.js";
let connection;
export const solDecimals = 9;
export const solTokenName = "SOL";
export const solEndpoint = "https://rpc.ankr.com/solana/c2d7e8a3db5dce62e202db3d28cca25e74da5028abbf20764e2961918ba34dfc";
export function getConnection() {
    if (connection) {
        return connection;
    }
    connection = new Connection(solEndpoint, "finalized");
    return connection;
}
export async function getSolFees() {
    try {
        const conn = getConnection();
        const { feeCalculator } = await conn.getLatestBlockhash();
        return { totalFee: feeCalculator.lamportsPerSignature.toString() };
    }
    catch (e) {
        return { totalFee: "0" };
    }
}
export async function createSolTx(fromAddress, toAddress, amount, mintAddress) {
    try {
        getConnection();
        const tx = new Transaction();
        const fromPublicKey = new PublicKey(fromAddress);
        const toPublicKey = new PublicKey(toAddress);
        if (!tx.feePayer) {
            tx.feePayer = fromPublicKey;
        }
        if (mintAddress) {
            const tokenPublicKey = new PublicKey(mintAddress);
            const fromTokenPubKey = await getAssociatedTokenAddress(tokenPublicKey, new PublicKey(fromAddress));
            const toTokenPubKey = await getAssociatedTokenAddress(tokenPublicKey, new PublicKey(toAddress));
            tx.recentBlockhash = (await connection.getLatestBlockhash()).blockhash;
            let account;
            try {
                account = await getAccount(connection, toTokenPubKey);
            }
            catch (error) {
                if (error instanceof TokenAccountNotFoundError || error instanceof TokenInvalidAccountOwnerError) {
                    try {
                        tx.add(createAssociatedTokenAccountInstruction(fromPublicKey, toTokenPubKey, toPublicKey, tokenPublicKey));
                    }
                    catch (error) { }
                }
                else {
                    throw error;
                }
            }
            console.log("account", account);
            tx.add(createTransferInstruction(fromTokenPubKey, toTokenPubKey, fromPublicKey, amount, [], TOKEN_PROGRAM_ID));
        }
        else {
            tx.recentBlockhash = (await connection.getLatestBlockhash()).blockhash;
            tx.add(SystemProgram.transfer({
                fromPubkey: fromPublicKey,
                toPubkey: toPublicKey,
                lamports: amount,
            }));
        }
        return tx;
    }
    catch (e) {
        return null;
    }
}
/**
 * Parses a Solana transaction and extracts relevant information.
 *
 * @param transaction The Solana transaction to parse.
 * @returns An object containing the transaction's from address, to address, amount, and mint address (if applicable).
 */
export async function parseSolTx(transaction) {
    let from = "";
    let to = "";
    let value = 0;
    let mintAddress;
    const connection = getConnection();
    console.log("parseSolTx", transaction);
    // 
    for (const instruction of transaction.instructions) {
        if (instruction.programId.equals(TOKEN_PROGRAM_ID) && instruction.data[0] === 3) {
            const amountData = instruction.data.slice(1, 9);
            value = Number(new BN(amountData, "le"));
            // 
            const fromTokenAccount = instruction.keys[0]?.pubkey;
            const toTokenAccount = instruction.keys[1]?.pubkey;
            try {
                // 
                const toAccount = await getAccount(connection, toTokenAccount);
                mintAddress = toAccount.mint.toString();
                to = toAccount.owner.toString(); // 
            }
            catch (error) {
                console.warn("Failed to get token account:", error);
            }
            // 
            if (!from && fromTokenAccount) {
                try {
                    const fromAccount = await getAccount(connection, fromTokenAccount);
                    from = fromAccount.owner.toString();
                }
                catch (error) {
                    console.warn("Failed to get source token account:", error);
                }
            }
        }
    }
    // 
    for (const instruction of transaction.instructions) {
        if (instruction.programId.equals(ASSOCIATED_TOKEN_PROGRAM_ID)) {
            const mintKey = instruction.keys[3]?.pubkey;
            const ownerKey = instruction.keys[2]?.pubkey;
            if (mintKey && ownerKey) {
                mintAddress = mintKey.toString();
                to = ownerKey.toString();
            }
        }
    }
    // SOL
    for (const instruction of transaction.instructions) {
        if (instruction.programId.equals(SystemProgram.programId) && instruction.data[0] === 2) {
            const fromPubkey = instruction.keys[0]?.pubkey.toString();
            const toPubkey = instruction.keys[1]?.pubkey.toString();
            const lamportsData = instruction.data.slice(4);
            value = Number(new BN(lamportsData, "le"));
            from = fromPubkey;
            to = toPubkey;
        }
    }
    console.log("parseSolTx.result", { from, to, value, mintAddress });
    return { from, to, value, mintAddress };
}
