    async sendCode(email) {
        try {
            const checkResp = await emailCheck(email);
            if (checkResp.result) {
                // sign up
                await sendRegister(email, this.clientId);
                const req = {
                    type: "signup",
                    email,
                };
                setEmailReq(JSON.stringify(req));
            }
            else {
                // sign in
                await loginCode(email, this.clientId);
                const req = {
                    type: "signin",
                    email,
                };
                setEmailReq(JSON.stringify(req));
            }
            return true;
        }
        catch (e) {
            return false;
        }
    }
    async verifyCode(code) {
        try {
            const req = getEmailReq();
            if (!req || !req.email || (req.type !== "signup" && req.type !== "signin")) {
                throw new Error("Invalid request");
            }
            let resp;
            if (req.type === "signup") {
                resp = await verifyRegister(req.email, code, this.clientId);
            }
            else {
                resp = await loginVerify(req.email, code, this.clientId);
            }
            const token = resp.result.token;
            const userId = resp.result?.userInfoDTO?.id;
            if (!token) {
                throw new Error("Login failed");
            }
            setLocalToken(this.clientId, token);
            setLocalUserId(this.clientId, "" + userId);
            setLocalLoginType(this.clientId, "email");
            removeEmailReq();
            clearUserInfo();
            return true;
        }
        catch (e) {
            return false;
        }
    }
