    async getUserSocialInfo() {
        try {
            const ret = await getSocialInfo();
            // create in 3 min and inviterBound is false
            const inviterBound = ret.result?.inviterBound;
            const currentTime = Date.now();
            const threeMinutes = 3 * 60 * 1000;
            const isInThreeMinutes = currentTime - ret?.result?.createTime <= threeMinutes;
            const isNewUser = !inviterBound && isInThreeMinutes;
            if (isNewUser) {
                const ownerUid = await getUserIdWithClientId(this.clientId);
                !!ownerUid.result && bindInviter(ownerUid.result);
            }
            return ret.result;
        }
        catch (e) {
            throw new Error("Failed to get user social info");
        }
    }
