    handleWebWalletInvoke = async (type, params) => {
        try {
            this.closeAllRelayWindow();
            switch (type) {
                case WebWalletInvokeType.ONRAMP:
                    this.openOnramp(params);
                    break;
                case WebWalletInvokeType.SWAP:
                    this.openSwap(params);
                    break;
                case WebWalletInvokeType.SEND:
                    this.openSend(params);
                    break;
                case WebWalletInvokeType.RECEIVE:
                    this.openReceive(params);
                    break;
                case WebWalletInvokeType.CHANGE_PAY_PIN:
                    this.openChangePayPin(params);
                    break;
            }
        }
        catch (e) {
            console.log("handleAction error:", e);
        }
    };
