import { constructMovementTxReq, constructSignReq, getUserInfoFromLocalFirst } from "../../utils/utils";
import { RequestTypes } from "../../constant";
import { signRequest } from "../../utils/communicator";
import { movementSignMessage, signMovementTx } from "../../utils/apiUtils";
import { buildAptosSignTransactionOutput, getAptos, getAptosClient, getMovementTransactionStatus, getRawTransactionFromTxn, sendMovementTx, } from "../../utils/movement";
var UserResponseStatus;
(function (UserResponseStatus) {
    UserResponseStatus["APPROVED"] = "Approved";
    UserResponseStatus["REJECTED"] = "Rejected";
})(UserResponseStatus || (UserResponseStatus = {}));
export class MovementProvider {
    address = null;
    publicKey = null;
    connected = false;
    isConnected = false;
    connection;
    core = null;
    chainId = 126;
    constructor() { }
    setCore(core) {
        this.core = core;
    }
    signTransaction = async (transaction, asFeePayer) => {
        if (!this.address) {
            await this.getAddress();
        }
        const rawTransaction = await getRawTransactionFromTxn(transaction);
        const request = constructMovementTxReq(this.core.clientId, {
            reqType: RequestTypes.sendTx,
            address: this.address,
            params: {
                type: "sign",
                transaction,
                rawTransaction,
            },
        });
        const data = (await signRequest(request, this.core));
        if (!data || data.toLowerCase() === "cancel") {
            throw new Error("Sign transaction failed.");
        }
        const signRes = await signMovementTx({ rawTransaction, mfa: data });
        console.log("signRes>>>", signRes);
        const signedTransaction = await buildAptosSignTransactionOutput(signRes.result);
        return {
            status: UserResponseStatus.APPROVED,
            args: signedTransaction,
        };
    };
    signMessage = async (input) => {
        const { message, nonce } = input;
        const fullMessage = `${message}${nonce}`;
        // const signature = await this.signTransaction(fullMessage);
        const request = constructSignReq(this.core.clientId, {
            chainType: "movement",
            reqType: RequestTypes.signMessage,
            address: this.address,
            message,
        });
        const data = await signRequest(request, this.core);
        if (!data || data.toLowerCase() === "cancel") {
            return {
                status: UserResponseStatus.REJECTED,
            };
        }
        const signMsgRes = await movementSignMessage({ message: fullMessage, mfa: data });
        console.log("signature", signMsgRes);
        return {
            status: UserResponseStatus.APPROVED,
            args: {
                address: this.address,
                fullMessage: fullMessage,
                message,
                nonce,
                prefix: "APTOS",
                signature: signMsgRes.result,
            },
        };
    };
    signAndSubmitTransaction = async (transaction) => {
        if (!this.address) {
            await this.getAddress();
        }
        const data = transaction.payload;
        const isTestnet = this.chainId === 250;
        const client = await getAptosClient(isTestnet);
        const { AccountAddress } = await getAptos();
        const txn = await client.transaction.build.simple({
            sender: AccountAddress.fromString(this.address),
            data,
        });
        const senderAuthenticator = await this.signTransaction(txn);
        const sendTx = await sendMovementTx({
            txn,
            senderAuthenticator: senderAuthenticator.args,
            isTestnet: isTestnet,
        });
        await getMovementTransactionStatus(sendTx.hash, isTestnet);
        return {
            status: UserResponseStatus.APPROVED,
            args: sendTx,
        };
    };
    getAddress = async () => {
        try {
            const data = await getUserInfoFromLocalFirst(this.core.clientId);
            if (!data.result || !data.result.movementAddress) {
                throw new Error("Get address key failed.");
            }
            this.address = data.result.movementAddress;
            return this.address;
        }
        catch (error) {
            console.error("Error getting address:", error);
            throw error;
        }
    };
    switchChain = async (chainId) => {
        this.chainId = chainId;
    };
    connect = async () => {
        // TODO: connect
    };
    disconnect = async () => { };
}
