import HttpConnection from "@walletconnect/jsonrpc-http-connection";
import { JsonRpcProvider } from "@walletconnect/jsonrpc-provider";
import EventEmitter from "eventemitter3";
import { recoverMessageAddress } from "viem";
import { RequestTypes } from "../../constant";
import { signRequest } from "../../utils/communicator";
import { tomoLog } from "../../utils/log";
import { constructSignReq, constructTxReq } from "../../utils/utils";
import { allChains } from "./chains";
import { getTomoChains } from "../../utils/apiUtils";
const walletActions = {
    eth_requestAccounts: "eth_requestAccounts",
    eth_accounts: "eth_accounts",
    wallet_watchAsset: "wallet_watchAsset",
    eth_addEthereumChain: "eth_addEthereumChain",
    eth_switchEthereumChain: "eth_switchEthereumChain",
    wallet_switchEthereumChain: "wallet_switchEthereumChain",
    personal_sign: "personal_sign",
    eth_signTypedData_v4: "eth_signTypedData_v4",
    wallet_getPermissions: "wallet_getPermissions",
    wallet_requestPermissions: "wallet_requestPermissions",
    eth_sendTransaction: "eth_sendTransaction",
    eth_sendRawTransaction: "eth_sendRawTransaction",
    personal_ecRecover: "personal_ecRecover",
    eth_swapTransaction: "eth_swapTransaction",
    eth_approveTransaction: "eth_approveTransaction",
    eth_chainId: "eth_chainId",
};
const LsChainIdKey = "TOMO_WALLET_EVM_CHAIN_ID";
export class EthereumProvider extends EventEmitter {
    jsonRpcProvider;
    chainConfig;
    core;
    chainList;
    chainListFromCDN;
    instanceId;
    initialPromise;
    connectUrl;
    constructor(params) {
        super();
        this.connectUrl = params?.connectUrl || "";
        this.instanceId = Date.now().toString().slice(0, -2);
        this.chainList = allChains;
        this.chainListFromCDN = [];
        const chainId = this.chainIdDecimal;
        this.initialPromise = this.initial(chainId);
    }
    async initial(chainId) {
        const chainConfig = await this.getChainConfig(chainId).catch((e) => {
            throw e;
        });
        this.chainConfig = chainConfig;
        this.jsonRpcProvider = new JsonRpcProvider(new HttpConnection(chainConfig.rpcUrl));
        this.connect();
    }
    async getChainConfig(chainId) {
        const chain = this.getChain(chainId);
        const chainConfig = { chainId: chainId, rpcUrl: "" };
        if (!chain) {
            let chainList = this.chainListFromCDN;
            // no chainList or don't find chain, get chainlist from cdn
            if (chainList.length === 0 || !chainList.find((chain) => chain.id === chainId)) {
                chainList = await this.getChainListFromCDN();
            }
            const chainObj = chainList.find((chain) => chain.id === chainId);
            if (chainObj) {
                chainConfig.rpcUrl = chainObj.rpcUrl;
            }
            else {
                throw new Error("Unsupported chain");
            }
        }
        else {
            chainConfig.rpcUrl = chain.rpcUrls.default.http[0];
        }
        return chainConfig;
    }
    async getChainListFromCDN() {
        const res = await getTomoChains(this.connectUrl).catch(() => {
            return [];
        });
        const chainListFromCDN = res
            .filter((chain) => chain.platformType === "EVM")
            .map((chain) => ({
            ...chain,
            rpcUrl: chain.rpcUrls[0],
            id: chain.chainId,
        }));
        this.chainListFromCDN = chainListFromCDN;
        return chainListFromCDN;
    }
    getChain(id) {
        return this.chainList.find((item) => item.id === id);
    }
    async connect() {
        await this.jsonRpcProvider.connect();
        this.jsonRpcProvider.connection.on("error", (error) => tomoLog.error(error));
    }
    async request(request, context) {
        tomoLog.debug(request, this.instanceId);
        await this.initialPromise;
        if (walletActions[request.method]) {
            return await this.processWalletAction(request);
        }
        return await this.jsonRpcProvider.request(request, context);
    }
    get chainIdDecimal() {
        let chainId = localStorage.getItem(LsChainIdKey) ?? 1;
        chainId = isNaN(Number(chainId)) ? 1 : Number(chainId);
        console.log("get chain id decimal", chainId);
        return chainId;
    }
    get chainIdHex() {
        const idHex = "0x" + this.chainIdDecimal.toString(16);
        return idHex;
    }
    async processWalletAction(request) {
        switch (request.method) {
            case walletActions.eth_requestAccounts:
                return await this.requestAddresses();
            case walletActions.eth_accounts:
                return await this.getAddresses();
            case walletActions.personal_sign:
                return await this.signMessage(request.params[0], request.params[1]);
            case walletActions.eth_signTypedData_v4:
                return await this.signTypedData(request.params[0], request.params[1]);
            case walletActions.eth_sendTransaction:
                return await this.sendTransaction(request.params[0]);
            case walletActions.eth_sendRawTransaction:
                return await this.sendRawTransaction( /* request.params[0] */);
            case walletActions.wallet_switchEthereumChain:
                return await this.switchChain(request.params[0].chainId);
            case walletActions.eth_chainId:
                return this.chainIdHex;
            case walletActions.personal_ecRecover:
                return await this.recoverMessageAddress(request.params[0], request.params[1]);
            case walletActions.eth_swapTransaction:
                return await this.swapTransaction(request.params[0]);
            case walletActions.eth_approveTransaction:
                return await this.approveTransaction(request.params[0]);
            default:
                throw new Error("Unsupported wallet action");
        }
    }
    sendAsync(payload, 
    // eslint-disable-next-line no-unused-vars
    callback) {
        this.request(payload)
            .then((data) => callback(null, data))
            .catch((e) => callback(e, null));
    }
    async getChainId() {
        return this.chainIdDecimal;
    }
    setCore(core) {
        this.core = core;
    }
    async switchChain(chainId) {
        const chainIdNum = Number(chainId);
        if (chainIdNum === this.chainIdDecimal) {
            return;
        }
        const chainConfig = await this.getChainConfig(chainIdNum).catch(() => {
            throw new Error("Unsupported chain");
        });
        await this.setChainConfig(chainConfig);
        console.log("switched chain", chainConfig);
        this.emit("networkChanged", chainId);
    }
    async setChainConfig(chainConfig) {
        localStorage.setItem(LsChainIdKey, String(chainConfig.chainId));
        this.chainConfig = chainConfig;
        this.jsonRpcProvider = new JsonRpcProvider(new HttpConnection(chainConfig.rpcUrl));
        await this.connect();
    }
    async getAddresses() {
        return await this.requestAddresses();
    }
    async requestAddresses() {
        if (this.core) {
            const address = await this.core.getEthAddress();
            return [address];
        }
        return [];
    }
    async signMessage(msg, address) {
        const request = constructSignReq(this.core.clientId, {
            reqType: RequestTypes.signMessage,
            address,
            message: msg,
            chainType: "evm",
        });
        const data = await signRequest(request, this.core);
        return data;
    }
    async signTypedData(address, msg) {
        const request = constructSignReq(this.core.clientId, {
            reqType: RequestTypes.signTypedMessage,
            address,
            message: msg,
            chainType: "evm",
        });
        const data = await signRequest(request, this.core);
        return data;
    }
    async sendTransaction(params) {
        console.log("chain config", this.chainConfig);
        const address = await this.core.getEthAddress();
        const chainId = this.chainIdDecimal;
        const request = constructTxReq(this.core.clientId, {
            reqType: RequestTypes.sendTx,
            chainId,
            rpcUrl: this.chainConfig.rpcUrl,
            address,
            params,
        });
        const data = await signRequest(request, this.core);
        return data;
    }
    async recoverMessageAddress(message, signature) {
        const address = await recoverMessageAddress({
            message,
            signature: signature,
        });
        return address;
    }
    async sendRawTransaction( /* signedTx: string */) {
        throw new Error("Not supported yet");
    }
}
