import { EventNames } from "../constant";
import { setTemporaryData } from "./apiUtils";
import { xEvents } from "./existedEvents";
import { getLocalLoginType, getLocalToken, getLocalUserId, setLocalPasskey, setLocalToken } from "./LocalstorageUtils";
import { tomoLog } from "./log";
import { strToBase64 } from "./utils";
import { openWindow, relayWindowList } from "./windowUtils";
import JSONBig from "json-bigint";
import { v4 as uuidv4 } from "uuid";
export async function checkPopupClosed(popupPromise_ish, events, eventName, eventId) {
    /** promise-ish to handle possible use in Electron */
    const popup = await popupPromise_ish;
    const checkPopup = () => {
        if (popup && popup.closed) {
            const currentClosedWindowIndex = relayWindowList.findIndex((win) => win == popup);
            if (currentClosedWindowIndex !== -1) {
                relayWindowList.splice(currentClosedWindowIndex, 1);
            }
            tomoLog.debug("Popup window closed");
            if (!xEvents.find((id) => id === eventId)) {
                // avoid emit repeated events
                events.emit(`${eventName}#${eventId}`, false);
                tomoLog.debug("events:", events.eventNames(), `${eventName}#${eventId}`);
            }
            clearInterval(checkInterval);
        }
        else {
            tomoLog.debug("Popup window is still open");
        }
    };
    const checkInterval = setInterval(checkPopup, 1000);
    return checkInterval;
}
export const signRequest = async (req, core) => {
    if (!req) {
        throw new Error("Invalid request");
    }
    if (!core) {
        throw new Error("Invalid sdk status");
    }
    // save req
    const reqString = strToBase64(JSONBig.stringify({
        ...req,
        userId: getLocalUserId(core.clientId),
        loginType: getLocalLoginType(core.clientId),
    }));
    await setTemporaryData({ eventId: req.eventId, value: reqString });
    return new Promise((resolve, reject) => {
        const popup = openWindow({
            url: `${core.relayBase}/request?eventId=${req.eventId}`,
            name: "Sign Request",
        });
        const listener = (data) => {
            tomoLog.debug("result:", data);
            if (data.error) {
                reject(data.error);
            }
            else {
                resolve(data.result || data.response || data);
            }
        };
        checkPopupClosed(popup, core.events, EventNames.signReq, req.eventId);
        core.events.once(`${EventNames.signReq}#${req.eventId}`, listener);
    });
};
export const mfaRequest = async (req, core) => {
    const loginType = getLocalLoginType(core.clientId);
    req.loginType = loginType;
    const reqString = strToBase64(JSONBig.stringify(req));
    const popup = openWindow({
        url: `${core.relayBase}/mfa?req=${reqString}`,
        name: "mfa Request",
    });
    return new Promise((resolve, reject) => {
        const listener = (data) => {
            tomoLog.debug("result:", data);
            core
                .getEthAddress()
                .then((evmAddress) => {
                if (data.error) {
                    setLocalPasskey(evmAddress, false);
                    reject(data.error);
                }
                else {
                    setLocalPasskey(evmAddress, true);
                    resolve(data.result);
                }
            })
                .catch((e) => {
                reject(e);
            });
        };
        checkPopupClosed(popup, core.events, EventNames.mfaReq, req.eventId);
        core.events.once(`${EventNames.mfaReq}#${req.eventId}`, listener);
    });
};
function handleMessage(event) {
    if (event.data.status === "READY") {
        const msg = {
            type: this.reqType,
            auth: {
                jwt: getLocalToken(this.clientId),
                userId: getLocalUserId(this.clientId),
                clientId: this.clientId,
            },
            ...(this.params || {}),
            reqString: this.reqString,
        };
        console.log("msg", msg);
        this.postMessage?.(msg, "*");
        return;
    }
    if (event.data.isSuccess && event.data.result) {
        this.onSuccess(event.data);
    }
    else if (event.data.isError) {
        this.onError(event.data);
    }
    else if (event.data.isReject) {
        this.onReject(event.data);
    }
    else if (event.data.setAuth) {
        setLocalToken(this.clientId, event.data.jwt);
    }
}
export async function setIframeToken(core) {
    // Create a hidden iframe to get jwt from relay page
    const { clientId } = core;
    const jwt = getLocalToken(clientId);
    const iframe = document.createElement("iframe");
    iframe.style.display = "none";
    iframe.src = `${core.relayBase}/normal`;
    console.warn(clientId, jwt, iframe);
    document.body.appendChild(iframe);
    try {
        // Wait for jwt from relay page
        return await new Promise((resolve) => {
            const messageHandler = (event) => {
                if (event.data.status === "READY") {
                    // Send auth request after receiving READY message
                    const msg = {
                        auth: {
                            userId: getLocalUserId(clientId),
                            clientId: clientId,
                            jwt,
                            setIframeToken: true,
                        },
                    };
                    iframe.contentWindow?.postMessage(msg, "*");
                    console.warn("setIframeToken: msg", msg);
                }
                else if (event.data.setTokenSuccess) {
                    window.removeEventListener("message", messageHandler);
                    resolve("");
                }
            };
            window.addEventListener("message", messageHandler);
        });
    }
    catch (e) {
        console.error("setIframeToken:", e);
    }
    finally {
        // Clean up iframe and event listener
        document.body.removeChild(iframe);
    }
}
export async function getAuth(core) {
    const jwt = getLocalToken(core.clientId);
    if (jwt) {
        return jwt;
    }
    // Create a hidden iframe to get jwt from relay page
    const iframe = document.createElement("iframe");
    iframe.style.display = "none";
    iframe.src = `${core.relayBase}/normal`;
    document.body.appendChild(iframe);
    try {
        // Wait for jwt from relay page
        return await new Promise((resolve) => {
            const messageHandler = (event) => {
                if (event.data.status === "READY") {
                    // Send auth request after receiving READY message
                    const msg = {
                        auth: {
                            clientId: core.clientId,
                        },
                    };
                    iframe.contentWindow?.postMessage(msg, "*");
                }
                else if (event.data.setAuth && event.data.jwt) {
                    window.removeEventListener("message", messageHandler);
                    resolve(event.data.jwt);
                    setLocalToken(core.clientId, event.data.jwt);
                }
                else if (event.data.isFail) {
                    window.removeEventListener("message", messageHandler);
                    resolve("");
                }
            };
            window.addEventListener("message", messageHandler);
        });
    }
    catch (e) {
        console.error("getAuth:", e);
    }
    finally {
        // Clean up iframe and event listener
        document.body.removeChild(iframe);
    }
}
export const normalRequest = async (req, core) => {
    const loginType = getLocalLoginType(core.clientId);
    req.loginType = loginType;
    const reqString = strToBase64(JSONBig.stringify(req));
    const popup = openWindow({
        url: `${core.relayBase}/normal`,
        name: "normal Request",
    });
    return new Promise((resolve, reject) => {
        /**
         * listen to the message from the popup wallet
         */
        const msgHandler = handleMessage.bind({
            clientId: core.clientId,
            core: core,
            reqString: reqString,
            postMessage: (data, ...args) => popup.postMessage?.(data, ...args),
            onSuccess: (data) => {
                console.log("event.data.isSuccess>>>", data.isSuccess, data.result);
                resolve(data.result);
            },
            onError: (data) => {
                console.log("event.data.isError>>>", data.isError, data.result);
                reject(data.error);
            },
            onReject: (data) => {
                console.log("event.data.isReject>>>", data.isReject, data.result);
                reject(data.result);
            },
        });
        window.addEventListener("message", msgHandler);
        const listener = (data) => {
            if (data.result) {
                resolve(data.result);
            }
            else {
                reject(data.error);
            }
            /**
             * remove the listener after the promise is resolved
             */
            window.removeEventListener("message", msgHandler);
        };
        checkPopupClosed(popup, core.events, EventNames.normalReq, req.eventId);
        core.events.once(`${EventNames.normalReq}#${req.eventId}`, listener);
    });
};
export const walletRequest = async (reqType, core, params) => {
    const walletDomain = core.walletDomain;
    const eventId = uuidv4();
    const popup = openWindow({
        url: `${walletDomain}`,
        name: "wallet Request",
        height: 740,
        width: 400,
    });
    return new Promise((resolve, reject) => {
        /**
         * listen to the message from the popup wallet
         */
        const msgHandler = handleMessage.bind({
            clientId: core.clientId,
            core: core,
            reqType,
            params,
            postMessage: (data, ...args) => popup.postMessage?.(data, ...args),
            onSuccess: (data) => {
                console.log("event.data.isSuccess>>>", data.isSuccess, data.result, reqType);
                resolve(data.result);
            },
            onError: (data) => {
                console.log("event.data.isError>>>", data.isError, data.result, reqType);
                reject(data.error);
            },
            onReject: (data) => {
                console.log("event.data.isReject>>>", data.isReject, data.result, reqType);
                reject(data.result);
            },
        });
        window.addEventListener("message", msgHandler);
        const listener = (data) => {
            if (data.result) {
                resolve(data.result);
            }
            else {
                reject(data.error);
            }
            /**
             * remove the listener after the promise is resolved
             */
            window.removeEventListener("message", msgHandler);
        };
        checkPopupClosed(popup, core.events, EventNames.normalReq, eventId);
        core.events.once(`${EventNames.normalReq}#${eventId}`, listener);
    });
};
