import { constructSignReq, constructSuiTxReq, getUserInfoFromLocalFirst } from "../../utils/utils";
import { RequestTypes } from "../../constant";
import { signRequest } from "../../utils/communicator";
import { suiSignPersonalMessage, suiSignTransaction } from "../../utils/apiUtils";
import { toBase64 } from "@mysten/sui/utils";
import { getTransactionBlockBytes, sendSuiTx } from "../../utils/sui";
import { Transaction } from "@mysten/sui/transactions";
export class SuiProvider {
    address;
    core = null;
    constructor() { }
    async getAddress() {
        try {
            const data = await getUserInfoFromLocalFirst(this.core.clientId);
            if (!data.result || !data.result.suiAddress) {
                throw new Error("Get address key failed.");
            }
            this.address = data.result.suiAddress;
            return this.address;
        }
        catch (error) {
            console.error("Error getting address:", error);
            throw error;
        }
    }
    setCore(core) {
        this.core = core;
    }
    signTransaction = async (input) => {
        try {
            if (!this.address) {
                await this.getAddress();
            }
            let transaction = await input.transaction.toJSON();
            try {
                if (!JSON.parse(transaction).sender) {
                    const txb = Transaction.from(transaction);
                    txb.setSender(this.address);
                    transaction = await txb.toJSON();
                }
            }
            catch (error) {
                console.error("Error setting sender:", error);
            }
            const request = constructSuiTxReq(this.core.clientId, {
                reqType: RequestTypes.signTx,
                address: this.address,
                params: {
                    type: "sign",
                    transaction,
                },
            });
            const data = await signRequest(request, this.core);
            if (!data || data.toLowerCase() === "cancel")
                throw "user cancel";
            const signRes = await suiSignTransaction({
                tx: transaction,
                url: "https://fullnode.mainnet.sui.io:443",
                mfa: data,
            });
            const { result: signature } = signRes;
            const transactionBlockBytes = await getTransactionBlockBytes({
                transaction,
                address: this.address,
                // @ts-expect-error input.transaction
                txRaw: input.transaction,
            });
            return { bytes: toBase64(transactionBlockBytes), signature };
        }
        catch (e) {
            console.error(e);
            throw e;
        }
    };
    signAndExecuteTransaction = async (input) => {
        try {
            const signRes = await this.signTransaction(input);
            const transaction = await input.transaction.toJSON();
            const transactionBlockBytes = await getTransactionBlockBytes({
                transaction,
                address: this.address,
                // @ts-expect-error input.transaction
                txRaw: input.transaction,
            });
            const sendParam = {
                bytes: transactionBlockBytes,
                signature: signRes.signature,
            };
            const sendRes = await sendSuiTx(sendParam);
            const json = JSON.stringify(sendRes.effects);
            const effectsBytes = new TextEncoder().encode(json);
            return {
                digest: sendRes.digest,
                effects: btoa(String.fromCharCode(...effectsBytes)),
                ...signRes,
            };
        }
        catch (e) {
            console.error(e);
            throw e;
        }
    };
    async signMessage(_input) {
        return await this.signPersonalMessage(_input);
    }
    async signPersonalMessage(_input) {
        try {
            const message = new TextDecoder().decode(_input.message);
            const request = constructSignReq(this.core.clientId, {
                chainType: "sui",
                reqType: RequestTypes.signMessage,
                address: this.address,
                message,
            });
            const data = await signRequest(request, this.core);
            if (!data || data.toLowerCase() === "cancel") {
                return "user cancel";
            }
            const signMsgRes = await suiSignPersonalMessage({ message, mfa: data });
            /** bytes: Base64 encoded message bytes */
            return { bytes: toBase64(_input.message), signature: signMsgRes.result };
        }
        catch (e) {
            console.error(e);
            throw e;
        }
    }
}
