import { tomoLog } from "./log";
export const LOCAL_USER_TOKEN = "local-user-token";
export const LOCAL_USER_INFO = "local-user-info";
export const LOCAL_EMAIL_LOGIN_REQ = "local-email-login-req";
export const TOMO_CACHE = "tomo-cache";
export const getTomoCache = () => {
    try {
        const cache = localStorage.getItem(TOMO_CACHE);
        return JSON.parse(cache ?? "");
    }
    catch (e) {
        return null;
    }
};
export const setTomoCache = (cache) => {
    try {
        localStorage.setItem(TOMO_CACHE, JSON.stringify(cache));
    }
    catch (e) {
        tomoLog.error("save cache failed");
    }
};
export const setCacheField = (clientId, key, value) => {
    let cache = getTomoCache();
    if (cache) {
        if (cache[clientId]) {
            cache[clientId][key] = value;
        }
        else {
            cache[clientId] = {
                [key]: value,
            };
        }
    }
    else {
        cache = {
            [clientId]: {
                [key]: value,
            },
        };
    }
    setTomoCache(cache);
};
//  token
const globalTokenStore = {};
export const getLocalToken = (clientId) => {
    try {
        return globalTokenStore[clientId] || null;
    }
    catch (e) {
        return null;
    }
};
export const setLocalToken = (clientId, token) => {
    globalTokenStore[clientId] = token;
};
export const removeLocalToken = (clientId) => {
    setCacheField(clientId, "token", null);
};
export const getLocalUserId = (clientId) => {
    try {
        const cacheObj = getTomoCache();
        return cacheObj?.[clientId]?.userId;
    }
    catch (e) {
        return null;
    }
};
export const setLocalUserId = (clientId, userId) => {
    setCacheField(clientId, "userId", userId);
};
export const removeLocalUserId = (clientId) => {
    setCacheField(clientId, "userId", null);
};
export const getLocalLoginType = (clientId) => {
    try {
        const cacheObj = getTomoCache();
        return cacheObj?.[clientId]?.loginType;
    }
    catch (e) {
        return null;
    }
};
export const setLocalLoginType = (clientId, loginType) => {
    setCacheField(clientId, "loginType", loginType);
};
export const removeLocalLoginType = (clientId) => {
    setCacheField(clientId, "loginType", null);
};
export const getLocalPasskey = (evmAddress) => {
    try {
        const cacheObj = getTomoCache();
        return cacheObj?.[evmAddress]?.localPasskey;
    }
    catch (e) {
        return undefined;
    }
};
export const setLocalPasskey = (evmAddress, localPasskey) => {
    setCacheField(evmAddress, "localPasskey", localPasskey);
};
export const getEmailReq = () => {
    try {
        const req = localStorage.getItem(LOCAL_EMAIL_LOGIN_REQ);
        return JSON.parse(req ?? "");
    }
    catch (e) {
        return null;
    }
};
export const setEmailReq = (req) => {
    try {
        localStorage.setItem(LOCAL_EMAIL_LOGIN_REQ, req);
    }
    catch (e) {
        return null;
    }
};
export const removeEmailReq = () => {
    try {
        localStorage.removeItem(LOCAL_EMAIL_LOGIN_REQ);
    }
    catch (e) {
        // do something
    }
};
