import { relayMessageTypes, relayOrigin } from "../constant";
import { tomoLog } from "./log";
import { handleGoogleAuth, handleKakaoAuth, handleMfaCreate, handleNormalReq, handleSignReq, handleTelegramAuth, handleTwitterAuth, } from "./handlerMessage";
const handlerMap = {
    [relayMessageTypes.signReq]: handleSignReq,
    [relayMessageTypes.mfaCreate]: handleMfaCreate,
    [relayMessageTypes.normalReq]: handleNormalReq,
    [relayMessageTypes.telegramAuth]: handleTelegramAuth,
    [relayMessageTypes.googleAuth]: handleGoogleAuth,
    [relayMessageTypes.xAuth]: handleTwitterAuth,
    [relayMessageTypes.kakaoAuth]: handleKakaoAuth,
};
export const messageProcessor = ({ origin, data: { type, data, action } }, core) => {
    tomoLog.debug("messageProcessor:", origin, type, data, action, core.relayBase);
    if (origin !== core.relayBase && origin !== relayOrigin) {
        return;
    }
    const handler = handlerMap[type];
    if (handler) {
        handler({ data, action }, core);
    }
};
