export async function getAptos() {
    const { AccountAddress, Aptos, APTOS_COIN, AptosConfig, Serializer, SimpleTransaction, Ed25519Signature, Ed25519PublicKey, AccountAuthenticatorEd25519, Network, AccountAuthenticator, } = await import("@aptos-labs/ts-sdk");
    return {
        AccountAddress,
        Aptos,
        APTOS_COIN,
        AptosConfig,
        Serializer,
        SimpleTransaction,
        Ed25519Signature,
        Ed25519PublicKey,
        AccountAuthenticatorEd25519,
        Network,
        AccountAuthenticator,
    };
}
export const getAptosClient = (() => {
    let client = null;
    return async (isTestnet = false) => {
        if (!client) {
            // Setup the client
            const { AptosConfig, Aptos, Network } = await getAptos();
            const config = new AptosConfig(isTestnet
                ? {
                    fullnode: testnetRpcUrl,
                    faucet: "https://faucet.testnet.bardock.movementnetwork.xyz/",
                }
                : {
                    fullnode: rpcUrl,
                    network: Network.CUSTOM,
                });
            client = new Aptos(config);
        }
        return client;
    };
})();
export const getRawTransactionFromTxn = async (txn) => {
    const { Serializer } = await getAptos();
    const serializer = new Serializer();
    txn.serialize(serializer);
    const serializedData = serializer.toUint8Array();
    const data = Buffer.from(serializedData).toString("hex");
    return data;
};
/** send tx */
export const sendMovementTx = async ({ txn, senderAuthenticator, isTestnet = false, }) => {
    const client = await getAptosClient(isTestnet);
    const committedTxn = await client.transaction.submit.simple({
        transaction: txn,
        senderAuthenticator,
    });
    return committedTxn;
};
export const buildAptosSignTransactionOutput = async (signRes) => {
    try {
        const { Ed25519Signature, Ed25519PublicKey, AccountAuthenticatorEd25519 } = await getAptos();
        const signResObj = JSON.parse(signRes);
        const { publicKey: publicKeyStr, sig } = signResObj;
        const signature = new Ed25519Signature(sig);
        const publicKey = new Ed25519PublicKey(publicKeyStr);
        const senderAuthenticator = new AccountAuthenticatorEd25519(publicKey, signature);
        return senderAuthenticator;
    }
    catch (error) {
        throw Error("sign error");
    }
};
/** get tx status */
export const getMovementTransactionStatus = async (hash, isTestnet = false) => {
    const client = await getAptosClient(isTestnet);
    const executedTransaction = await client.waitForTransaction({
        transactionHash: hash,
    });
    return executedTransaction;
};
