import { Connection, PublicKey } from "@solana/web3.js";
import { RequestTypes } from "../../constant";
import { signRequest } from "../../utils/communicator";
import { constructSignReq, constructSolTxReq, getUserInfoFromLocalFirst } from "../../utils/utils";
export class SolanaProvider {
    publicKey = null;
    connected = false;
    isConnected = false;
    connection;
    endpoint = "https://rpc.ankr.com/solana/c2d7e8a3db5dce62e202db3d28cca25e74da5028abbf20764e2961918ba34dfc";
    core = null;
    constructor(endpoint) {
        if (endpoint) {
            this.endpoint = endpoint;
        }
    }
    async getPublicKey() {
        const data = await getUserInfoFromLocalFirst(this.core.clientId);
        if (!data.result || !data.result.solAddress) {
            throw new Error("Get public key failed.");
        }
        this.publicKey = new PublicKey(data.result.solAddress);
        return this.publicKey.toBase58();
    }
    async getAddress() {
        return await this.getPublicKey();
    }
    async getBalance() {
        await this.connect();
        const address = await this.getPublicKey();
        return await this.connection?.getBalance?.(new PublicKey(address));
    }
    setCore(core) {
        this.core = core;
    }
    // async sendSol(
    //   to: string,
    //   amount: string,
    //   mintAddress?: string,
    // ): Promise<string> {
    //   await this.getPublicKey();
    //   const request = constructSolTxReq(this.core.clientId, {
    //     reqType: RequestTypes.sendTx,
    //     address: this.publicKey.toBase58(),
    //     params: {
    //       to,
    //       value: amount,
    //       mintAddress,
    //     },
    //   });
    //   const data: any = await signRequest(request, this.core);
    //   return data;
    // }
    // async swapSol(params: SwapTxParams) {
    //   await this.getPublicKey();
    //   const request = constructSolTxReq(this.core.clientId, {
    //     reqType: RequestTypes.swapTx,
    //     address: this.publicKey.toBase58(),
    //     params,
    //   });
    //   const data = await signRequest(request, this.core);
    //   return data;
    // }
    async signTransaction(transaction) {
        await this.getPublicKey();
        const txHex = await this.getTransactionHex(transaction);
        const request = constructSolTxReq(this.core.clientId, {
            reqType: RequestTypes.sendTx,
            address: this.publicKey.toBase58(),
            params: {
                type: "sign",
                transaction: txHex,
            },
        });
        const data = await signRequest(request, this.core);
        return data;
    }
    // async signAllTransactions<T extends Transaction | VersionedTransaction>(
    //   transactions: T[],
    // ): Promise<T[]> {
    //   await this.getPublicKey();
    //   const txs = [];
    //   for (const tx of transactions) {
    //     const txHex = await this.getTransactionHex(tx);
    //     txs.push(txHex);
    //   }
    //   const request = constructSolTxReq(this.core.clientId, {
    //     reqType: RequestTypes.sendTx,
    //     address: this.publicKey.toBase58(),
    //     params: {
    //       transactions: txs,
    //     },
    //   });
    //   const data: any = await signRequest(request, this.core);
    //   return data;
    // }
    async sendTransaction(transaction, connection) {
        await this.getPublicKey();
        const txHex = await this.getTransactionHex(transaction, connection);
        const request = constructSolTxReq(this.core.clientId, {
            reqType: RequestTypes.sendTx,
            address: this.publicKey.toBase58(),
            params: {
                type: "send",
                transaction: txHex,
            },
        });
        const data = await signRequest(request, this.core);
        return data;
    }
    async signMessage(message) {
        await this.getPublicKey();
        const messageStr = new TextDecoder().decode(message);
        const request = constructSignReq(this.core.clientId, {
            chainType: "solana",
            reqType: RequestTypes.signMessage,
            address: this.publicKey.toBase58(),
            message: messageStr,
        });
        const data = await signRequest(request, this.core);
        return data;
    }
    async connect() {
        this.connection = new Connection(this.endpoint, "confirmed");
        this.connected = true;
        this.connected = true;
    }
    async disconnect() {
        this.connection = null;
        this.isConnected = false;
        this.isConnected = false;
    }
    async getTransactionHex(transaction, connection) {
        let txHex;
        if (transaction.serializeMessage) {
            const tx = transaction;
            if (!tx.feePayer) {
                if (this.publicKey) {
                    tx.feePayer = this.publicKey;
                }
                else {
                    this.publicKey = new PublicKey(await this.getPublicKey());
                    tx.feePayer = this.publicKey;
                }
            }
            if (!this.isConnected) {
                await this.connect();
            }
            const conn = connection || this.connection;
            if (conn.getLatestBlockhash) {
                tx.recentBlockhash = (await conn.getLatestBlockhash())?.blockhash;
            }
            else {
                tx.recentBlockhash = (await conn.getRecentBlockhash("max"))?.blockhash;
            }
            txHex = tx.serialize({ requireAllSignatures: false, verifySignatures: false }).toString("hex");
        }
        else {
            // txHex = Buffer.from(transaction.serialize()).toString('hex')
            throw new Error("Unsupported transaction type, only support Transaction");
        }
        return txHex;
    }
}
