import { Transaction } from "@mysten/sui/transactions";
import { SUI_TYPE_ARG } from "@mysten/sui/utils";
const suiEndPoint = "https://fullnode.mainnet.sui.io:443";
export const SUI_TOKEN_ADDRESS = SUI_TYPE_ARG;
let suiClient;
const getSuiClient = async () => {
    if (suiClient) {
        return suiClient;
    }
    const client = await import("@mysten/sui/client");
    suiClient = new client.SuiClient({ url: suiEndPoint });
    return suiClient;
};
export const sendSuiTx = async ({ bytes, signature, }) => {
    const suiClient = await getSuiClient();
    const res = await suiClient.executeTransactionBlock({
        transactionBlock: bytes,
        signature: signature,
    });
    return res;
};
export const getTransactionBlockBytes = async ({ transaction, txRaw, address, }) => {
    let tx;
    let transactionBlockBytes;
    const suiClient = await getSuiClient();
    try {
        tx = Transaction.from(transaction);
        if (!JSON.parse(transaction).sender) {
            tx.setSender(address);
        }
        transactionBlockBytes = await tx.build({ client: suiClient });
    }
    catch (error) {
        txRaw.setSenderIfNotSet(address);
        transactionBlockBytes = await txRaw.build({ client: suiClient });
    }
    return transactionBlockBytes;
};
