import { RequestTypes } from "../../constant";
import { signRequest } from "../../utils/communicator";
import { constructSignReq, constructTronTxReq, getUserInfoFromLocalFirst } from "../../utils/utils";
export class TronProvider {
    address = null;
    tronWeb;
    tronWebPromise;
    core = null;
    constructor() { }
    setCore(sdk) {
        this.core = sdk;
    }
    async getAddress() {
        try {
            const data = await getUserInfoFromLocalFirst(this.core.clientId);
            if (!data.result || !data.result.tronAddress) {
                throw new Error("Get address key failed.");
            }
            this.address = data.result.tronAddress;
            return this.address;
        }
        catch (error) {
            console.error("Error getting address:", error);
            throw error;
        }
    }
    async signMessage(message, privateKey) {
        // const messageStr = new TextDecoder().decode(message);
        if (!this.address) {
            await this.getAddress();
        }
        const request = constructSignReq(this.core.clientId, {
            chainType: "tron",
            reqType: RequestTypes.signMessage,
            address: this.address,
            message,
        });
        const data = await signRequest(request, this.core);
        return data;
    }
    async signTransaction(transaction) {
        if (!this.address) {
            await this.getAddress();
        }
        const request = constructTronTxReq(this.core.clientId, {
            reqType: RequestTypes.sendTx,
            address: this.address,
            params: {
                type: "sign",
                transaction,
            },
        });
        const data = await signRequest(request, this.core);
        return data;
    }
}
