export const handleTwitterAuth = async ({ data, action }, core) => {
    if (!data)
        return;
    if (!shouldHandle(data.eventId))
        return;
    tomoLog.debug("xEvents:", xEvents);
    try {
        if (data?.code) {
            let result;
            if (action === LoginAction.Link) {
                result = await core.linkTwitter(data.code);
            }
            else {
                result = await core.loginByTwitter(data.code);
            }
            core.events.emit(`${EventNames.xAuth}#${data.eventId}`, result);
        }
        else if (data?.error) {
            core.events.emit(`${EventNames.xAuth}#${data.eventId}`, false);
        }
    }
    catch (error) {
        core.events.emit(`${EventNames.xAuth}#${data.eventId}`, false);
    }
    finally {
        removeEvent(data.eventId);
    }
};
export const handleKakaoAuth = async ({ data }, core) => {
    if (!data.code)
        return;
    if (!shouldHandle(core.kakaoAuthEventId))
        return;
    const postData = querystring.stringify({
        grant_type: "authorization_code",
        client_id: kakaoJsKey,
        redirect_uri: kakaoRedirectUri,
        code: data.code,
    });
    try {
        const resp = await axios.post("https://kauth.kakao.com/oauth/token", postData, {
            headers: {
                "Content-Type": "application/x-www-form-urlencoded",
            },
        });
        if (!resp?.data?.access_token) {
            console.error("kakao access_token is empty");
            throw new Error("access_token is empty");
        }
        console.log("log in with kakao code");
        const result = await core.loginByKakao(resp.data.access_token);
        core.events.emit(`${EventNames.kakaoAuth}#${core.kakaoAuthEventId}`, result);
    }
    catch (error) {
        core.events.emit(`${EventNames.kakaoAuth}#${core.kakaoAuthEventId}`, false);
    }
    finally {
        removeEvent(core.kakaoAuthEventId);
    }
};
