import axios from "axios";
import { version } from "../../package.json";
import { redirectUriForX } from "../constant";
import { getAuth } from "./communicator";
// export const BASE_TEST_URL = "https://app.unyx.tech/api/";
export const BASE_TEST_URL = "https://fans-tech-backend.tomo.services/api/";
export const BASE_PROD_URL = "https://apps-prod.tomo.inc/api/";
let baseUrl = BASE_PROD_URL;
let api;
export const setApi = (core) => {
    const { connectUrl: baseURL } = core;
    baseUrl = baseURL;
    api = axios.create({ baseURL });
    api.interceptors.request.use(async (config) => {
        const token = await getAuth(core);
        if (token) {
            config.headers = config.headers || {};
            config.headers.Authorization = `Bearer ${token}`;
        }
        config.headers["Tomo-Sdk-Version"] = version;
        return config;
    }, (error) => {
        if (error?.response?.status === 401) {
            return Promise.reject(error);
        }
    });
};
export function setHeaders(headers) {
    if (!api)
        throw "api not initialized yet";
    api.interceptors.request.use((config) => {
        Object.entries(headers).forEach(([key, value]) => {
            config.headers[key] = value;
        });
        return config;
    });
}
