export const suiSignTransaction = async (params) => {
    const { data } = await api
        .post(`socialLogin/projectWallet/sui/signTransaction`, params, {
        headers: {
            mfa: params.mfa,
        },
    })
        .catch((error) => {
        const message = error?.response?.data?.message;
        return Promise.reject(message || "Failed to sign transaction");
    });
    return data;
};
