import { relayMessageTypes } from "../constant";
import { isMobile } from "./device";
export const relayWindowList = [];
export const openWindow = ({ url, name, width, height }) => {
    const top = (window.innerHeight - (height || 400)) / 2 + window.screenY;
    const left = (window.innerWidth - (width || 400)) / 2 + window.screenX;
    if (relayWindowList.length) {
        const currentWindow = relayWindowList[relayWindowList.length - 1];
        currentWindow.location.href = url;
        return currentWindow;
    }
    try {
        const relyWindow = window.open(url, name, `dialog=yes,top=${top}px,left=${left},width=${width !== undefined ? width : 400}px,height=${height !== undefined ? height : 600}px`);
        // Fallback to iframe modal if:
        // 1. window.open is blocked by browser
        // 2. iOS Safari requires user interaction for window.open
        if (!relyWindow) {
            return createModalIframe({ url, width });
        }
        relayWindowList.push(relyWindow);
        return relyWindow;
    }
    catch (error) {
        console.error("Failed to open window:", error);
        return createModalIframe({ url, width });
    }
};
export const injectIframe = ({ url }) => {
    const id = "tomo-social-relay";
    const existed = document.getElementById(id);
    if (existed) {
        existed.remove();
    }
    const iframe = document.createElement("iframe");
    iframe.style.display = "none";
    iframe.src = url;
    iframe.id = id;
    iframe.allow = "publickey-credentials-create *";
    document.body.appendChild(iframe);
    return iframe;
};
const modalIframe = {
    closed: false,
};
/**
 * Opens a modal iframe to display a web page.
 * @param {object} props
 * @param {string} props.url The URL to open in the iframe.
 * @param {number} [props.width=400] The width of the iframe.
 * @returns {HTMLIFrameElement} The iframe element.
 */
const createModalIframe = ({ url, width = 400 }) => {
    const id = "tomo-social-iframe-modal";
    const existed = document.getElementById(id);
    if (existed) {
        existed.remove();
    }
    // Save original states
    const originalMeta = document.querySelector('meta[name="viewport"]');
    const originalMetaContent = originalMeta?.getAttribute("content");
    const originalBodyStyle = {
        overflow: document.body.style.overflow,
        position: document.body.style.position,
        width: document.body.style.width,
        height: document.body.style.height,
    };
    const isMobileDevice = isMobile();
    const overlay = document.createElement("div");
    overlay.style.cssText = `
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background: transparent;
    z-index: 999999;
    -webkit-overflow-scrolling: touch;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
  `;
    const container = document.createElement("div");
    const containerWidth = width || 400;
    const containerHeight = isMobileDevice ? "100%" : "600px";
    const containerStyle = isMobileDevice
        ? `
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    max-width: 100%;
    max-height: 100%;
  `
        : `
    width: ${containerWidth}px;
    height: ${containerHeight};
    border-radius: 16px;
  `;
    container.style.cssText = `
    ${containerStyle}
    background: white;
    overflow: hidden;
    z-index: 1000000;
    display: flex;
    flex-direction: column;
    margin: 0;
    padding: 0;
    border: none;
    box-shadow: ${isMobileDevice ? "none" : "0 4px 24px rgba(0, 0, 0, 0.1)"};
    opacity: 0;
    transition: opacity 0.3s;
  `;
    // Create iframe with enhanced security
    const iframe = document.createElement("iframe");
    iframe.id = id;
    iframe.setAttribute("loading", "eager");
    iframe.setAttribute("importance", "high");
    // Security attributes
    iframe.setAttribute("sandbox", "allow-scripts allow-forms allow-same-origin allow-popups allow-modals allow-storage-access-by-user-activation");
    iframe.setAttribute("referrerpolicy", "strict-origin");
    iframe.allow = "publickey-credentials-create; clipboard-write";
    iframe.src = url;
    iframe.style.cssText = `
    width: 100%;
    height: 100%;
    border: none;
    margin: 0;
    padding: 0;
    flex: 1;
    display: block;
    background: white;
    -webkit-overflow-scrolling: touch;
  `;
    // Show content when loaded
    iframe.onload = () => {
        container.style.opacity = "1";
    };
    if (isMobileDevice) {
        const meta = document.createElement("meta");
        meta.name = "viewport";
        meta.content = "width=device-width, initial-scale=1.0, maximum-scale=1.0, user-scalable=no, viewport-fit=cover";
        document.head.appendChild(meta);
        document.body.style.overflow = "hidden";
        document.body.style.position = "fixed";
        document.body.style.width = "100%";
        document.body.style.height = "100%";
    }
    container.appendChild(iframe);
    overlay.appendChild(container);
    document.body.appendChild(overlay);
    const cleanup = () => {
        overlay.remove();
        if (isMobileDevice) {
            const currentMeta = document.querySelector('meta[name="viewport"]');
            if (currentMeta) {
                if (originalMeta && originalMetaContent) {
                    originalMeta.setAttribute("content", originalMetaContent);
                }
                else {
                    currentMeta.remove();
                }
            }
            document.body.style.overflow = originalBodyStyle.overflow;
            document.body.style.position = originalBodyStyle.position;
            document.body.style.width = originalBodyStyle.width;
            document.body.style.height = originalBodyStyle.height;
        }
    };
    // Handle messages
    const messageHandler = (event) => {
        if (Object.values(relayMessageTypes).includes(event.data.type)) {
            cleanup();
            window.removeEventListener("message", messageHandler);
        }
    };
    window.addEventListener("message", messageHandler);
    // Handle overlay click
    overlay.addEventListener("click", (e) => {
        if (e.target === overlay) {
            modalIframe.closed = true;
            cleanup();
            window.removeEventListener("message", messageHandler);
        }
    });
    modalIframe.closed = false;
    return modalIframe;
};
