import EventEmitter from "eventemitter3";
import { RequestTypes } from "../../constant";
import { getBtcFeeRate, getBtcInscriptionData, getBtcInscriptionInfo, pushBtcTx } from "../../utils/apiUtils";
import { signRequest } from "../../utils/communicator";
import { tomoLog } from "../../utils/log";
import { constructBtcTxReq, constructSignReq, getUserInfoFromLocalFirst } from "../../utils/utils";
import { getBalanceFromNode } from "./apiUtils";
import { AddressTypeMap, addressInfos, btcChains, networkMap } from "./type";
export class BitcoinProvider {
    network = networkMap.mainnet;
    event = new EventEmitter();
    addressType = AddressTypeMap.P2TR;
    address = "";
    pubKey = "";
    core;
    // connectWallet() {
    //   return this;
    // }
    setCore(core) {
        this.core = core;
    }
    // getWalletProviderName() {
    //   return "TOMO Social Wallet";
    // }
    async getAccounts() {
        return [await this.getAddress()];
    }
    async getAddress() {
        const data = await getUserInfoFromLocalFirst(this.core.clientId);
        if (!data.result) {
            throw new Error("Get address failed.");
        }
        if (this.network === networkMap.mainnet) {
            this.address = data.result.btcMainnetAddressInfo[this.addressType.toLowerCase()];
            return this.address;
        }
        else {
            this.address = data.result.btcTestnetAddressInfo[this.addressType.toLowerCase()];
            return this.address;
        }
    }
    async getPublicKey() {
        return this.getPublicKeyHex();
    }
    async getPublicKeyHex() {
        const data = await getUserInfoFromLocalFirst(this.core.clientId);
        if (!data.result) {
            throw new Error("Get public key failed.");
        }
        const key = this.addressType.toLowerCase() + "PubKey";
        if (this.network === networkMap.mainnet) {
            return data.result.btcMainnetAddressInfo[key];
        }
        else {
            return data.result.btcTestnetAddressInfo[key];
        }
    }
    async getAllAddresses() {
        const data = await getUserInfoFromLocalFirst(this.core.clientId);
        if (!data.result) {
            throw new Error("Get address failed.");
        }
        let info;
        if (this.network === networkMap.mainnet) {
            info = data.result.btcMainnetAddressInfo;
        }
        else {
            info = data.result.btcTestnetAddressInfo;
        }
        const ret = Object.entries(info)
            .map(([key, value]) => {
            const addressInfo = addressInfos.find((x) => x.label === key.toUpperCase());
            if (!addressInfo) {
                return null;
            }
            return {
                ...addressInfo,
                address: value,
            };
        })
            .filter((x) => x !== null);
        return ret;
    }
    async sendBitcoin(to, satAmount, feeRate) {
        const request = constructBtcTxReq(this.core.clientId, {
            reqType: RequestTypes.sendTx,
            network: this.network,
            address: this.address,
            addressType: this.addressType,
            params: {
                to,
                amount: satAmount,
                feeRate,
                fromAddress: await this.getAddress(),
                pubKey: await this.getPublicKeyHex(),
            },
        });
        const data = await signRequest(request, this.core);
        return data;
    }
    async signPsbt(psbtHex) {
        const request = constructBtcTxReq(this.core.clientId, {
            reqType: RequestTypes.signTx,
            network: this.network,
            address: this.address,
            addressType: this.addressType,
            params: {
                psbtHex,
            },
        });
        const data = await signRequest(request, this.core);
        return data;
    }
    async signPsbts(psbtHexs) {
        const request = constructBtcTxReq(this.core.clientId, {
            reqType: RequestTypes.signTxs,
            network: this.network,
            address: this.address,
            addressType: this.addressType,
            params: {
                psbtHexs,
            },
        });
        const data = await signRequest(request, this.core);
        return data ? data.split(",") : "";
    }
    async signMessageBIP322(message) {
        const request = constructSignReq(this.core.clientId, {
            chainType: "bitcoin",
            reqType: RequestTypes.signMessage,
            address: this.address,
            addressType: this.addressType,
            network: this.network,
            message,
            signMessageType: "bip322-simple",
        });
        const data = await signRequest(request, this.core);
        return data;
    }
    async signMessageEcdsa(message) {
        const request = constructSignReq(this.core.clientId, {
            chainType: "bitcoin",
            reqType: RequestTypes.signMessage,
            address: this.address,
            addressType: this.addressType,
            network: this.network,
            message,
            signMessageType: "ecdsa",
        });
        const data = await signRequest(request, this.core);
        return data;
    }
    async signMessage(message, _type = "ecdsa") {
        return _type === "ecdsa" ? this.signMessageEcdsa(message) : this.signMessageBIP322(message);
    }
    getNetwork() {
        return this.network;
    }
    getAddressType() {
        return this.addressType;
    }
    async switchNetwork(network) {
        if (network !== "mainnet" && network !== "testnet" && network !== "signet") {
            throw new Error("Invalid network, only support mainnet, testnet, signet");
        }
        this.network = network;
        await this.getAddress();
        this.event.emit("networkChanged", network);
    }
    async changeAddressType(addressType) {
        if (!AddressTypeMap[addressType]) {
            throw new Error("Invalid address type, only support P2PKH, P2WPKH, P2TR, P2SH");
        }
        this.addressType = AddressTypeMap[addressType];
        await this.getAddress();
        this.event.emit("addressTypeChanged", addressType);
    }
    on(eventName, callBack) {
        this.event.on(eventName, callBack);
    }
    off(eventName, callBack) {
        this.event.off(eventName, callBack);
    }
    // Mempool calls
    async getBalance(address) {
        try {
            const btcAddress = address || (await this.getAddress());
            const btcRes = await getBalanceFromNode(btcChains[this.network].apiBlockstream, btcAddress);
            // eslint-disable-next-line @typescript-eslint/no-explicit-any
            const chain_stats = btcRes?.chain_stats;
            // eslint-disable-next-line @typescript-eslint/no-explicit-any
            const mempool_stats = btcRes?.mempool_stats;
            const btcBalance = chain_stats.funded_txo_sum -
                chain_stats.spent_txo_sum +
                (mempool_stats.funded_txo_sum - mempool_stats.spent_txo_sum);
            return btcBalance;
        }
        catch (e) {
            return 0;
        }
    }
    async getFeeRate() {
        try {
            const data = await getBtcFeeRate(this.network.toUpperCase());
            return data.result;
        }
        catch (e) {
            tomoLog.debug("Get fee rate failed:", e);
            throw new Error("Get fee rate failed.");
        }
    }
    async pushTx(txHex) {
        try {
            const data = await pushBtcTx(txHex, this.network.toUpperCase());
            return data.result;
        }
        catch (e) {
            tomoLog.debug("Get fee rate failed:", e);
            throw new Error("Get fee rate failed.");
        }
    }
    async getInscriptionData(address, network = this.network) {
        try {
            const data = await getBtcInscriptionData(address, network.toUpperCase());
            return data.result;
        }
        catch (e) {
            tomoLog.debug("GetInscriptionData failed:", e);
            throw new Error("GetInscriptionData failed.");
        }
    }
    async getInscriptionInfo(insId, network = this.network) {
        try {
            const data = await getBtcInscriptionInfo(insId, network.toUpperCase());
            return data.result;
        }
        catch (e) {
            tomoLog.debug("GetInscriptionInfo failed:", e);
            throw new Error("GetInscriptionInfo failed.");
        }
    }
}
