import { v4 as uuidv4 } from "uuid";
import { getLocalToken } from "./LocalstorageUtils";
import { getUserInfo as getUserInfoFromRemote } from "./apiUtils";
import { tomoLog } from "./log";
let userInfo = null;
export const getUserInfoFromLocalFirst = async (clientId) => {
    if (userInfo) {
        tomoLog.debug("cache data:", userInfo);
        return userInfo;
    }
    userInfo = await getUserInfoFromRemote(clientId);
    return userInfo;
};
export const clearUserInfo = () => {
    userInfo = null;
};
export const isHexStrict = (hex) => typeof hex === "string" && /^((-)?0x[0-9a-f]+|(0x))$/i.test(hex);
export const strToBase64 = (str) => {
    const bytes = new TextEncoder().encode(str);
    const binString = Array.from(bytes, (byte) => String.fromCodePoint(byte)).join("");
    return btoa(binString);
};
export const base64ToStr = (base64) => {
    const binString = atob(base64);
    const data = Uint8Array.from(binString, (m) => m.codePointAt(0));
    return new TextDecoder().decode(data);
};
export const constructSignReq = (clientId, params) => {
    const { reqType, chainType, address, addressType, network, message, signMessageType } = params;
    return {
        eventId: uuidv4(),
        chainType: chainType,
        origin: window.location.origin,
        accessToken: getLocalToken(clientId),
        reqType,
        signInfo: {
            message,
            address,
            addressType,
            network,
            signMessageType,
        },
    };
};
export const constructTxReq = (clientId, params) => {
    const { reqType, chainId, rpcUrl } = params;
    return {
        eventId: uuidv4(),
        chainType: "evm",
        origin: window.location.origin,
        accessToken: getLocalToken(clientId),
        reqType,
        txMeta: {
            txParams: params.params,
            chainId,
            rpcUrl,
        },
    };
};
